.top{
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100vh;

  @include mobile{
    height: 650px;
  }

  @include laptop{
    height: 1000px;
  }

  .top-inner{
    position: relative;
    background-color: $bg2-color;
    width: 100%;
    height:100vh;

    @include laptop{
      height: 1000px;
    }

    &::after{
      height: 0;
      display: block;
      position: absolute;
      right: -3px;
      content: "";
      border-bottom: solid 200px #fff;
      border-left: solid 2000px transparent;
      top: 80%;
      z-index: 2;

      @include mobile{
        border-bottom: solid 120px #fff;
        border-left: solid 1000px transparent;
        top: 55%;
      }
    }

    .top-content{
      .top-img1 {
        position: absolute;
        top: 40%;
        left: 35%;
        transform: translate(-50%, -50%);
        width: 723px;
        height: 237px;

        @include mobile{
          top: 18%;
          width: 310px;
          object-fit: contain;
        }

        @include laptop{
          top: 25%;
          left:50%;
        }
      }

      .content-inner{
        position: absolute;
        top: 80%;
        left: 30%;
        transform: translate(-50%, -50%);
        display:flex;
        align-items: center;
        justify-content: center;
        width: 428px;
        height: 68px;
        border-radius:56px;
        background:$gradient;
        z-index: 20;

        &:hover {
          cursor: pointer;
          opacity: 0.6;
          transition: 0.3s;
        }

        @include mobile{
          top: 55%;
          width: 320px;
          height: 45px;
        }

        @include laptop{
          top: 90%;
          left: 50%;
          z-index: 10;
        }

        .top-link{
          display: block;
          font-size:26px;
          color:#FFF;
          font-weight: bold;

          @include mobile{
            font-size: 20px;
          }
        }

        .arrow{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 25px;
          height: 25px;
          border: 2px solid #FFF;
          border-radius: 50%;
          margin-left: 20px;

          @include mobile{
            width: 15px;
            height: 15px;
            margin-left: 10px;
            opacity: 0.8;
          }

          p{
          color:#FFF;
          font-weight: bolder;

          @include mobile{
            font-size: 12px;
          }
          }
        }
      }
    
    .top-img2{
        position: absolute;
        top: 60%;
        left: 65%;
        transform: translate(-50%, -50%);
        width: 507px;
        height: 498px;
        z-index: 10;

        @include mobile{
          top: 35%;
          width: 320px;
          height: 290px;
          object-fit: contain;
        }

        @include laptop{
          top: 55%;
          left: 50%;
        }
      }
    }
  }
}

