#recommend{
  .sub-ttl::before,
  .sub-ttl::after {
    content: '';
    width: 2px;
    height: 25px;
    background-color: $sub-ttl-color;
    display: inline-block;
    vertical-align: middle;
  }

  .sub-ttl::before {
    margin-right: 10px;
    transform: rotate(-35deg);  
  }

  .sub-ttl::after {
    margin-left: 10px;
    transform: rotate(35deg);
  }

  .recommend-content{
    display: flex;
    margin-top: 20px;

    @include mobile{
      display: block;
    }
    .panel{
      margin:10px;
      margin-bottom: 0;
      width: 507px;

      @include mobile{
        width: 310px;
        margin-right: auto;
        margin-left: auto;
      }

      .panel-ttl{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 250px;
        height: 40px;
        background-color: $panel-color;
        border-top-right-radius: 15px;

        .panel-icon{
          width: 40px;
          height: 40px;
          margin-right: 10px;
          margin-top: 10px;
        }

        .panel-text{
          font-size:20px;
          font-weight: bold;
          letter-spacing: 5px;
          margin-top: 8px;

          @include mobile{
            font-size: 18px;
          }
        }
      }

      .panel-inner{
        display: flex;
        background-color: $panel-color;
        padding:20px;

        @include mobile{
          height: 200px;
          padding-right: 5px;
          padding-left: 5px;
        }

        .inner-text{
          p{
            margin-top: 10px;
            letter-spacing: 3px;
            line-height: 30px;
            font-size: 16px;
            font-weight: bold;
            display: flex;
            align-items: flex-start;

            &::before{
              content: url(../asset/img/02_check.png);
              width: 15px;
              height:15px;
              object-fit: cover;
              margin-right: 3px;
            }

            @include mobile{
              margin-top: 15px;
              letter-spacing: 3px;
              line-height: 20px;
            }

            @include tablet{
              font-size: 16px;
              letter-spacing: 3px;
              line-height: 20px;
            }
          }

          p:nth-of-type(1){
            @include mobile{
            width: 290px;
            }
          }
          p:nth-of-type(2) {

            @include mobile{
            position: relative;
            width: 200px;
            margin-right: 0;
            }
          }
        }
        img{
          margin-top: 85px;
          width: 157px;
          height: 171px;

          @include mobile{
            position: absolute;
            left:61.5%;
          }
          

          @include tablet{
            width: 109px;
            height: 119px;
          }
        }
      }
    }
  }
}
.recommend-bottom{
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height:auto;
  background-repeat: no-repeat;
  object-fit: content;
  margin-top: 110px;
  margin-bottom: 83px;

  @include mobile{
    margin-bottom: 20px;
  }

  img{
    height: 257px;
    object-fit: cover;
    @include mobile{
      height: 240px;
      object-fit: cover;
    }
  }

  &::after {
    height: 0;
    display: block;
    position: absolute;
    content: "";
    border-bottom: solid 80px #fff;
    border-left: solid 80px transparent;
    transform: rotate(45deg);
    top: -17%;
    left:45%;
    z-index: 2;

    @include mobile{
      top: -13%;
      left: 45%;
      border-bottom: solid 50px #fff;
      border-left: solid 50px transparent;
    }

    @include tablet{
      top: -15%;
      left: 45%;
      border-bottom: solid 50px #fff;
      border-left: solid 50px transparent;
    }

    @include laptop{
      top: -30%;
      left: 45%;
      border-bottom: solid 80px #fff;
      border-left: solid 80px transparent;
    }
  }

  P{
    position: absolute;
    text-align: center;
    color:#FFF;
    font-size:33px;
    letter-spacing: 3px;
    line-height: 50px;
    text-shadow: 0px 3px 6px #00000088;

    @include mobile{
      font-size: 20px;
      line-height: 40px;
    }

    @include tablet{
      font-size: 26px;
    }

    span{
      font-family: 'Times New Roman', Times, serif;
      font-size: 39px;

      @include mobile{
        font-size: 26px;
      }
    }

    span{
      display: inline-block;
    }
  }
}

