#profile {
  margin-top: 66px;
  padding: 58px 0;
  background-color:$bg2-color;
  display: flex;

  @include mobile{
    padding: 20px 0;
  }

  .profile-wrapper {
    margin-top: 20px;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    width: 813px;
    height: 417px;
    border: solid 3px $sub-ttl-color;
    background-color:#FFF;

    @include mobile{
      width: 300px;
      height: 350px;
    }

    @include tablet{
      width: 680px;
    }

    .content-ttl{
      font-family: 'Times New Roman',Times,serif;
      font-weight: normal;
      font-size: 50px;
      position: absolute;
      top:-7%;
      left:40%;
      line-height: 1;
      padding: 0 20px;
      background: linear-gradient(180deg, $bg2-color 0%, $bg2-color 50%, #FFF 50%, #FFF 100%);

      @include mobile{
        font-size: 36px;
        left: 25%;
        top: -7%;
      }
    }

    .profile-content{
      margin: 40px;

      @include mobile{
        margin: 10px;
        margin-top: 20px;
      }

      .profile-ttl{
        display: flex;
        align-items: center;

        @include mobile{
          flex-direction: row-reverse;
        }

        img{
          width: 110px;
          height: 110px; 
          object-fit: cover;  
          @include mobile{
            width: 90px;
            height: 90px;
            z-index: 10 ;
          }
        }

        h3{
          margin-left: 26px;
          font-size: 26px;
          font-weight: bold;
          line-height: 30px;

          @include mobile{
            font-size: 16px;
            margin-left: 0px;
            line-height: 25px;
            margin-right: 20px;
          }
          span{
            font-size: 16px;
            @include mobile{
              font-size: 13px;
            }
          }
        }
      }

      .profile-inner{
        p{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 108px;
          height: 29px;
          font-size: 16px;
          background-color: #5c5c5d;
          color: #FFF;
          margin-top: 30px;
          @include mobile{
            margin-top: 15px;
            font-size: 12px;
            height: 20px;
            width: 70px;
            font-weight: bold;
          }
        }

        ol{
          margin-top: 23px;
          @include mobile{
            margin-top: 10px;
          }

          li{
            font-size: 16px;
            line-height: 30px;
            display: flex;
            align-items: center;

            @include mobile{
              font-size: 12px;
              line-height: 20px;
              width: 280px;
              margin-right: auto;margin-left: auto;
              flex-wrap: wrap;
            }

            @include tablet{
              font-size: 13px;
            }

            &::before{
              content:"";
              display: inline-block;
              width: 10px;
              height: 10px;
              margin-right: 10px;
              background-color: #5c5c5d;
              transform: rotate(45deg);

              @include mobile{
                width: 8px;
                height: 8px;
                margin-right: 5px;
                margin-bottom: 18px;
              }
            }

            &:nth-child(5)::before {
              @include mobile{
                margin-bottom: 0px;
              }
            }

            &:nth-child(2)::before{
              opacity: 0;
            }

            br {
              display: none;

              @include mobile{
                display: block;
              }
            }
          }
        }
      }
    }
  }
}

#contact {
  margin-top: 66px;
  padding: 58px 0;

  @include mobile{
    padding: 20px 0;
    margin-top: 20px;
  }

  .content-ttl {
    font-family: 'Times New Roman', Times, serif;
    font-weight: normal;
    font-size: 50px;

    @include mobile{
      font-size: 36px;
    }
  }

  .contact-wrapper {
    display: flex;
    justify-content: center;

    form{
      dl{

        .required {
          &::after {
            content: "※";
            color: $sub-ttl-color;
            margin-left: 5px;
          }
        }
        dt{
          font-size: 18px;
          font-weight: bold;
          margin-top: 25px;
          margin-bottom: 10px;

          #modal-open{
            color:#8193b2;
            height: 30px;
            border-bottom:2px solid #8193b2;

            &:hover{
              cursor: pointer;
            }
          }
        }

        dt:last-of-type{
          margin-top: 10px;
        }

        dd{
          margin: 0;
          input{
            height: 10px;
            background-color: #F1F1F1;
            border: none;
            padding: 15px;

            @include mobile{
              padding: 5px;
              height: 20px;
            }

            &::placeholder{
              font-size: 18px;
              color: #9D9A9A;

              @include mobile{
                font-size: 14px;
              }
            }
          }

          textarea{
            width: 850px;
            height:148px;
            background-color: #F1F1F1;
            border: none;
            padding: 15px;

            @include mobile{
              width: 290px;
              padding: 5px;
            }

            @include tablet{
              width: 600px;
            }

            &::placeholder {
              font-size: 18px;
              color: #9D9A9A;

              @include mobile{
                font-size: 14px;
              }
            }
          }
        }
        dd:nth-of-type(1){
          input{
            width: 850px;

            @include mobile{
              width: 290px;
            }

            @include tablet{
              width: 600px;
            }
          }
        }
        dd:nth-of-type(2), 
        dd:nth-of-type(3)
          {
          input {
            width: 500px;

            @include mobile {
              width: 290px;
            }
          }
        }
        dd:nth-of-type(4) {
          input {
            width: 300px;

            @include mobile{
              width: 290px;
            }
          }
        }
        dd:last-of-type {
          margin-top: 25px;
          input[type="checkbox"] {
            display: none;
          }

          input[type="checkbox"]+label {
            display: none;
            cursor: pointer;
            display: inline-block;
            position: relative;
            padding-left: 25px;
            padding-right: 10px;
          }

          input[type="checkbox"]+label::before {
            content: "";
            position: absolute;
            display: block;
            box-sizing: border-box;
            width: 20px;
            height: 20px;
            margin-top: -10px;
            left: 0;
            top: 50%;
            border: 3px solid;
            border-color: #585753;
            /* 枠の色変更 お好きな色を */
            background-color: #FFF;
            /* 背景の色変更 お好きな色を */
          }

          input[type="checkbox"]:checked+label::after {
            content: "";
            position: absolute;
            display: block;
            box-sizing: border-box;
            width: 18px;
            height: 9px;
            margin-top: -9px;
            top: 50%;
            left: 3px;
            transform: rotate(-45deg);
            border-bottom: 2px solid;
            border-left: 3px solid;
            border-color: #8193b2;
            /* チェックの色変更 お好きな色を */
          }
          label{
            font-size: 14px;
            font-weight: bold;
          }
        }
      }

      .contact-btn-wrapper {
        margin-top: 42px;
        margin-right: auto;
        margin-left: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 151px;
        height: 68px;
        border-radius: 56px;
        background: $gradient;

        &:hover {
          cursor: pointer;
          opacity: 0.6;
          transition: 0.3s;
        }

        @include mobile{
          width: 100px;
          height: 40px;
        }

        .contact-btn {
          font-size: 26px;
          color: #FFF;
          font-weight: bold;
          background:none;
          border:none;

          &:hover {
            cursor: pointer;
          }


          @include mobile{
            font-size: 18px;
          }
        }
      }
    }
  }
}

