
.footer {
  text-align: center;
  padding: 60px 150px 0 150px;
  height: 522px;
  background-image: url(../asset/img/bg3.png);

  @include mobile{
    padding:60px 0 0 0;
  }

  .footer-content{
    margin:0 auto;
    writing-mode:vertical-rl;

    p{
      display: flex;
      margin:28px;
    }
  }

  .footer-logo img {
    width: 40px;
    height: 40px;
    box-shadow: none;
    margin: 66px 0 20px 0;
  }
  
  .footer-title{
    font-weight: bold;
  }

  .footer-address{
    display: flex;
    justify-content: center;
    margin-top:50px;

    p{
      margin:10px;
    }
  }

  .footer-num {
    display: flex;
    justify-content: center;

    p{
      margin:10px;
    }
  }
}

.footer-end{
  background-color:#000;
  color:white;
  height:60px;
  display: flex;
  align-items: center;
  justify-content: center;
}