.link-btn{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 428px;
  height: 68px;
  border-radius: 56px;
  background: $gradient;

  &:hover{
    cursor: pointer;
    opacity: 0.6;
    transition: 0.3s;
  }

  @include mobile{
    width: 300px;
    height: 40px;
  }

  .link-inner {
    font-size: 26px;
    color: #FFF;
    font-weight: bold;

    @include mobile{
      font-size: 18px;
    }
  }

  .link-arrow {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 25px;
    height: 25px;
    border: 2px solid #FFF;
    border-radius: 50%;
    margin-left: 20px;

    p {
      color: #FFF;
      font-weight: bolder;
    }
  }
}