
#modal-content {
  display: none;
  position: fixed;
  width: 923px;
  height:640px;
  margin: 1.5em auto 0;
  padding: 10px 20px;
  background: #fff;
  z-index: 9000;
  border: 5px solid #8193b2;

  @include mobile{
    width: 280px;
    margin-top: 0.5em;
    padding: 5px 10px;
  }

  @include tablet{
    width: 600px;
    height: 400px;
  }

  @include laptop{
    height: 500px;
  }

  .modal-p {
    margin-top: 1em;
  }

  .modal-p:first-child {
    margin-top: 0;
  }

  .btn-close {
    position: relative;
    float:left;

    &::before{
      position: absolute;
      content:"";
      display: inline-block;
      width: 3px;
      height: 20px;
      background-color: #000;
      transform: rotate(-45deg);
    }

    &::after {
      position: absolute;
      content: "";
      display: inline-block;
      width: 3px;
      height: 20px;
      background-color: #000;
      transform: rotate(45deg);
    }

  }

  .btn-close:hover {
    cursor: pointer;
  }

  .modal-inner{
    margin-right: auto;
    margin-left: auto;
    width: 70%;
    height:640px;
    font-size: 34px;
    letter-spacing: 5px;
    line-height: 40px;
    font-family: "ヒラギノ角ゴ Pro W3" ;
    overflow: auto;
    padding:20px;

    @include mobile{
      width: 100%;
      padding: 0;
      margin-top: 30px;
      line-height: 23px;
    }

    @include tablet{
      height: 350px;
    }

    @include laptop{
      height: 450px;
    }

    p{
      margin-right: auto;
      margin-left: auto;

      @include mobile{
        width: 280px;
        font-size: 11px;
      }
    }
  }
}

#modal-overlay {
  z-index: 8000;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 120%;
  background-color: rgba(0, 0, 0, 0.75);
}

