.header{
  position:fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width:100%;
  height: 80px;
  padding: 0 20px;
  z-index: 1000;

  @include mobile{
    padding: 0 5px;
  }

  .mb-navwrapper{
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 20px;

    @include mobile{
      padding-right: 0;
    }

    .mb-logo{

      @include mobile{
      width: 100%;
      }

      img{
        object-fit: cover;
        z-index: 10;

        @include mobile{
          width: 100%;
          object-fit: contain;
        }
      }
    }

    .mb-nav-right{
      display: flex;
      align-items: center;
    }
  }

  .mb-link-btn {
    display: none;

    @include mobile{
      width: 80px;
      height: 45px;
      margin-left: 10px;
      margin-right: 5px;
    }

    @include laptop{
      display: flex;
      align-items: center;
      justify-content: center;
      width:242px;
      height: 68px;
      border-radius: 56px;
      background: $gradient;
      margin-left: 0px;
      margin-right: 40px;
    }

    .mb-contact {
      font-size: 26px;
      color: #FFF;
      font-weight: bold;

      @include mobile{
        font-size: 10px;
      }
    }
  }
}

.nav-wrapper{
  display: flex;
  justify-content: flex-end;
  width:100%;

  @include mobile{
    padding: 0;
  }

  @include laptop{
    /*---- フェードインの場合は有効に ----*/
    /* opacity: 0; */

    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    right: -120%;
    z-index: 2;

    /*---- フェードインの場合は無効に ----*/
    transition: all 0.6s;
  }
}

.header-nav{
  display:flex;
  justify-content: space-between;
  padding:0 30px;

  @include mobile{
    justify-content: center;
  }

  @include laptop {
    width: 100%;
    height: 100%;
    background:$gradient-mb;
    z-index: 2;
  }

  .nav-list {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-around;

    @include laptop {
      display: block;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-align: center;
      font-size: 18px;
    }
    
    li{
      width: 105px;

      @include mobile{
        height: 25px;
        margin-top: 25px;
      }

      @include laptop{
        width: 100%;
        margin-top: 30px;
      }
    }

    li:nth-child(2){
      width: 130px;
      margin-right: 10px;

      @include laptop {
        width: 100%;
        margin-top: 98px;
      }
    }

    li:nth-child(4) {
      width: 75px;

      @include laptop {
        width: 100%;
        margin-top: 30px;
      }
    }

    li:nth-child(8){
      width: 60px;  
      margin-right: 10px;

      @include laptop{
        width: 100%;
        margin-right: 0;
      }
      a{
        @include laptop {
          font-size: 0;
        }

        &::before {
          display: none;

          @include mobile{
            font-size: 20px;
          }

          @include laptop{
            content: "Marketing and Oneとは";
            display:inline-block;
            font-size: 24px;
          }
        }
      }
    }

    .nav-item {

      @include laptop{
        width: 100%;
      }

      a{
        font-weight: bold;
        font-size:14px;

        @include mobile{
          font-size: 20px;
        }

        @include laptop{
          opacity: 0.7;
          color:#FFF;
          font-size: 26px;
        }
      }
    }

    .nav-item-mb{
      display: none;
      position: relative;

      @include laptop{
        display: block;
      }

      a {
        opacity: 0.5;
        font-family: 'Times New Roman', Times, serif;
        color:#FFF;
        font-size: 32px;
        letter-spacing: 3px;

        @include mobile{
          font-size: 24px;
        }
      }

      &::after{
        position: absolute;
        top:200%;
        right:45%;
        content: "";
        display: inline-block;
        width: 59px;
        height: 5px;
        background-color: #fff;
        opacity: 0.5;

        @include mobile{
          top: 150%;
          right: 40%;
        }
      }
    }

    .contact{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 157px;
      height: 36px;
      border-radius:56px;
      background: $gradient;

      &:hover {
        cursor: pointer;
        opacity: 0.6;
        transition: 0.3s;
      }

      @include mobile{
        width: 300px;
        height: 45px;
      }

      @include laptop{
        width: 500px;
        height: 65px;
        background: #FFF;
      }

      a {
        font-size: 19px;
        color:#FFF;
        display: flex;
        align-items: center;

        @include mobile{
          font-size: 0;
        }

        @include laptop{
          font-size: 0;
          color: #6F91C8;
        }

        &::before{
          display: none;

          @include mobile{
            font-size: 20px;
          }

          @include laptop{
            display: inline-block;
            vertical-align: middle;
            content:"お問い合わせはこちらから";
            font-size: 32px;
          }
        }
      }

      .arrow-mb{
        display:none;

        @include laptop{
          display: flex;
          align-items: center;
          justify-content: center;
          width: 20px;
          height: 20px;
          border: 3px solid #6F91C8;
          border-radius: 50%;
          margin-left: 20px;
          opacity: 0.5;
          p {
            color: #6F91C8;
            font-weight: bolder;
          }
        }

        @include mobile{
          width: 15px;height: 15px;
          margin-left: 10px;
        }
      }
    }
  }
}

.burger-btn {
  display: none;
}

@include mobile{
  .burger-btn{
    left: 10%;
  }
}


  @include laptop{
  .burger-btn {
    display: block;
    width: 39px;
    height: 39px;
    position: relative;
    z-index: 3;

    .bar {
      width: 25px;
      height: 2px;
      display: block;
      position: absolute;
      margin-right: 200px;
      transform: translateX(-50%);
      background-color: #000;
    }

    .bar_top {
      top: 10px;
    }

    .bar_mid {
      top: 50%;
      transform: translate(-50%, -50%);
    }

    .bar_bottom {
      bottom: 10px;
    }
  }

  .burger-btn.close .bar_top {
    transform: translate(-50%, 10px) rotate(45deg);
    transition: transform .3s;
    background-color: #FFF;
  }

  .burger-btn.close .bar_mid {
    opacity: 0;
    transition: opacity .3s;
    background-color: #FFF;
  }

  .burger-btn.close .bar_bottom {
    transform: translate(-50%, -7px) rotate(-45deg);
    transition: transform .3s;
    background-color: #FFF;
  }

  .nav-fadein {
    right: 0;
  }

}
