
html {
  font-size: 10px;
}

body {
  font-family:
  "Yu Gothic",
  "游ゴシック",
  YuGothic,
  "游ゴシック体",
  "ヒラギノ角ゴ Pro W3",
  "メイリオ",
  sans-serif;
  color: #2b3039;
}


.wrapper {
  max-width: 1040px;
  margin-right: auto;
  margin-left: auto;
  overflow: hidden;

  @include tablet{
    max-width: 680px;
  }

  @include laptop{
    max-width:900px;
  }
}

img {
  width: 100%;
}

h1 {
  font-size: 3.6rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3.6rem;
  font-weight: normal;
}


p{
  font-size: 1.6rem;
}

a {
  text-decoration: none;
  color:#000;
}

li {
  list-style: none;
}

section.anchor {
  display: block;
  padding-top: 70px;
  margin-top: -70px;
}

span.anchor{
  display: block;
  padding-top: 70px;
  margin-top: -70px;
}